    [ACTIONS.DOUBLE_STANDARD_FINISH.id]: 2,
    [ACTIONS.TECHNICAL_FINISH.id]: 3,
    [ACTIONS.SINGLE_TECHNICAL_FINISH.id]: 3,
    [ACTIONS.DOUBLE_TECHNICAL_FINISH.id]: 3,
    [ACTIONS.TRIPLE_TECHNICAL_FINISH.id]: 3,
